//引入vue和vuex
import Vue from 'vue'
import Vuex from 'vuex'
//使用vuex
Vue.use(Vuex)

const state = {
    messageList: [],
}

const getters = {
    getMessages () {
        return state.messageList
    }
}

const actions = {
    getMessages (ctx, data) {  // 接收消息
        ctx.commit('getMessages', data)
    }
}

const mutations = {
    getMessages (state, data) {
        state.messageList.push(data)
        console.log(state.messageList)
    }
}

export default new Vuex.Store({
    state,
    getters,
    actions,
    mutations
})


