<template>
    <div class="register">
        <div class="content">
            <h2 class="title">欢迎注册密语聊天室</h2>
            <form ref="register_from">
                <div class="form-group">
                    <input type="text" name="username" placeholder="请输入用户名">
                </div>
                <div class="form-group">
                    <input type="text" name="nickname" placeholder="请输入昵称">
                </div>
                <div class="form-group">
                    <input type="tel" maxlength="11" name="phone" placeholder="请输入电话">
                </div>
                <div class="form-group">
                    <input type="password" name="password" placeholder="请输入密码">
                </div>
                <div class="form-group">
                    <button type="button" class="btn" @click="register">注册</button>
                </div>
                <div class="form-group">
                    <p>
                        如果还没拥有账号，请前往
                        <router-link to="/login">登陆</router-link>
                    </p>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { Toast } from 'vant'
export default {
    name: 'login',
    methods: {
        register () {
            const params = new FormData(this.$refs.register_from)
            this.$server.register(params).then((res) => {
                if (res) {
                    Toast.success('注册成功')
                } else {
                    Toast.fail('注册失败')
                    return
                }
            })
        }
    }
}
</script>

<style scoped>
.register {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 20px auto;
}
.content {
    width: 80%;
    margin: 150px auto;
    background: #fff;
    box-sizing: border-box;
    padding: 5px;
    border: 1px solid #ddd;
    box-shadow: 0px 0px 12px #ddd;
    text-align: center;
}
.content .title {
    color: #52c1f9;
    font-size: 21px;
}
.content form {
    box-sizing: border-box;
    padding: 10px;
}
form .form-group {
    width: 100%;
    margin-bottom: 10px;
}
form .form-group input {
    width: 100%;
    padding: 6px 12px;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 4px #ddd;
    font-size: 14px;
    outline: none;
}
form .form-group .btn {
    width: 100%;
    height: 100%;
    padding: 6px 12px;
    background: #fff;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 4px #ddd;
    color: #52c1f9;
    font-size: 16px;
    outline: none;
}
form .form-group a {
    color: #52c1f9;
}
</style>



