<template>
    <div class="chatRoom">
        <div class="header">
            <div class="header-item">
                <i class="iconfont icon-fanhui" @click="goBack"></i>
            </div>
            <div class="header-item" style="text-align:  center;">
                {{nickname}}
            </div>
            <div class="header-item" style="text-align: right;">
                <i class="iconfont icon-gengduo-copy"></i>
            </div>
        </div>
        <div class="content">
            <ul ref="chatContent" id="chatContent" v-for="(item, index) in getMessage" :key="index">
                <li class="chat-peer" v-if="item.userID == peerID">
                    <div class="acatar-peer">
                        <img src="../img/avatar-peer.png" width="30" height="30" alt="">
                    </div>
                    <div class="chat-peer-content">
                        <span class="left"></span>
                        <p style="max-width: 200px;">{{item.message}}</p>
                    </div>
                </li>
                <li class="chat-me" v-if="item.userID == userID && item.peerID == peerID">
                    <div class="chat-me-content">
                        <span class="right"></span>
                        <p style="max-width: 200px;" >{{item.message}}</p>
                    </div>
                    <div class="acavtar-me">
                        <img src="../img/avatar-default.png" width="30" height="30" alt="">
                    </div>
                </li>
            </ul>
            
        </div>
        <div class="footer">
            <input type="text" name="" v-model="message" @keyup.enter="sendMessage">
            <button type="button" @click="sendMessage">发送</button>
        </div>
    </div>  
</template>

<script>
import { Toast } from 'vant'
import tool from 'tool/tool'
import websock from 'websock/websock'
export default {
    name: 'chatRoom',
    data () {
        return {
            userID: JSON.parse(sessionStorage.getItem('user')).userID,
            userAcavtar: JSON.parse(sessionStorage.getItem('user')).headImg,
            peerID: tool.getUrlKey('peerID'),
            nickname: tool.getUrlKey('nickname'),
            message: '',
            peerMessage: ''
        }
    },
    methods: {
        sendMessage () {  // 发信息   
            if (this.message != '') {
                const sendInfo = this.message+'?'+this.peerID
                websock.websocketsend(sendInfo)
                const obj = {
                    userID: this.userID,
                    peerID: this.peerID,
                    message: this.message
                }
                this.$store.dispatch('getMessages', obj)
                // const chatMe = ` 
                //     <li class="chat-me" style=" margin-bottom: 10px; display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-box-align: center; -ms-flex-align: center; align-items: center;-webkit-box-pack: end;-ms-flex-pack: end;justify-content: flex-end;">
                //         <div class="chat-me-content" style="position: relative;-webkit-box-sizing: border-box;box-sizing: border-box;padding: 5px;background: #52c1f9;color: #fff;margin-right: 5px;">
                //             <span class="right" style=" width: 0; height: 0; border-width: 6px 0 6px 6px;border-style: solid;border-color: transparent transparent transparent #52c1f9;position: absolute; right: -5px;"></span>
                //             <p style="max-width: 200px;">${this.message}</p>
                //         </div>
                //         <div class="acavtar-me" style="box-sizing: border-box;padding: 5px; width:  40px;height: 40px;background:  #fff;">
                //             <img src="/img/${this.userAcavtar}" width="30" height="30" alt="">
                //         </div>
                //     </li>`
                // $('#chatContent').append(chatMe)
                this.message = ''
            } else {
                Toast.fail('请输入发送的内容')
                return
            }
           
        },
        goBack () {
            history.go(-1)
        }
    },
    computed: {
        getMessage () {
           return this.$store.getters.getMessages
        }
    },
    watch: {
       
    }
}
</script>

<style scoped>
.chatRoom {
    padding: 50px 0px;
    height: 100%;
}
.header {
    background: #fff;
    width: 100%;
    height: 45px;
    border-bottom: 1px solid #ddd;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}
.header .header-item {
    flex: 1;
    box-sizing: border-box;
    padding: 10px;
} 
.footer {
    width: 100%;
    height: 45px;
    background: #fff;
    border-top: 1px solid #ddd;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    box-sizing: border-box;
    padding: 6px;
}
.footer input {
    width: 80%;
    border: 1px solid #ddd;
    outline: none;
    padding: 6px 12px;
    margin-right: 10px;
}
.footer button {
    border: none;
    background: #52c1f9;
    color: #fff;
    padding: 6px 12px;
    outline: none;
    width: 20%;
}
.content ul li {
    margin-bottom: 10px;
}
li.chat-peer {
    display: flex;
}
.acatar-peer {
    box-sizing: border-box;
    padding: 5px;
}
.chat-peer-content {
    position: relative;
    box-sizing: border-box;
    padding: 5px;
    background: #fff;
    margin-left: 5px;
    word-wrap: break-word;
}
span.left {
    position: absolute;
    width: 0;
    height: 0;
    border-width: 6px 6px 6px 0;
    border-style: solid;
    border-color: transparent #fff transparent transparent;
    left: -5px;
}
li.chat-me {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.chat-me-content {
    position: relative;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 5px;
    background: #52c1f9;
    color: #fff;
    margin-right: 5px;
    word-wrap: break-word;
}
span.right {
    width: 0;
    height: 0;
    border-width: 6px 0 6px 6px;
    border-style: solid;
    border-color: transparent transparent transparent #52c1f9;
    position: absolute;
    right: -5px;
}
.acavtar-me {
    box-sizing: border-box;
    padding: 5px;
}
</style>



