<template>
    <div class="login">
        <div class="content">
            <h2 class="title">登陆密语聊天室</h2>
            <form ref="login_from">
                <div class="form-group">
                    <input type="text" name="username" placeholder="请输入用户名" v-model="loginInfo.username">
                </div>
                <div class="form-group">
                    <input type="password" name="password" placeholder="请输入密码" v-model="loginInfo.password" @keyup.enter="login">
                </div>
                <div class="form-group">
                    <button type="button" class="btn" @click="login">登陆</button>
                </div>
                <div class="form-group">
                    <p>
                        如果还没拥有账号，请前往
                        <router-link to="/register">注册</router-link>
                    </p>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import { Toast } from 'vant'
import websock from 'websock/websock'
export default {
    name: 'login',
    data () {
        return {
            loginInfo: {
                username: '',
                password: ''
            }
        }
    },
    methods: {
        login () {
            const params = new FormData(this.$refs.login_from)
            this.$server.login(params).then((res) => {
                if (!res) {
                    Toast.fail('用户名或者密码错误')
                    return
                } else {
                    Toast.success('登陆成功')
                    sessionStorage.setItem('user', JSON.stringify(res))
                    var userID = JSON.parse(sessionStorage.getItem('user')).userID
                    websock.initWebSocket(userID)
                    this.$router.push({path: '/'})
                }
            })
        }
    }
}
</script>

<style scoped>
.login {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 20px auto;
}
.content {
    width: 80%;
    margin: 150px auto;
    background: #fff;
    box-sizing: border-box;
    padding: 5px;
    border: 1px solid #ddd;
    box-shadow: 0px 0px 12px #ddd;
    text-align: center;
}
.content .title {
    color: #52c1f9;
    font-size: 21px;
}
.content form {
    box-sizing: border-box;
    padding: 10px;
}
form .form-group {
    width: 100%;
    margin-bottom: 10px;
}
form .form-group input {
    width: 100%;
    padding: 6px 12px;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 4px #ddd;
    font-size: 14px;
    outline: none;
}
form .form-group .btn {
    width: 100%;
    height: 100%;
    padding: 6px 12px;
    background: #fff;
    border: 1px solid #ddd;
    box-shadow: 2px 2px 4px #ddd;
    color: #52c1f9;
    font-size: 16px;
    outline: none;
}
form .form-group a {
    color: #52c1f9;
}
</style>



