<template>
    <div class="mHeader">
        <h4>{{ title }}</h4>
    </div>
</template>

<script>
export default {
    name: 'mHeader',
    props: {
        title: {
            type: String,
            default: ''
        }
    }
}
</script>

<style scoped>
.mHeader {
    width: 100%;
    position: fixed;
    height: 45px;
    top: 0;
    left: 0;
    box-shadow: 0px 0px 12px #ddd;
    border-bottom: 1px solid #ddd;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
}
</style>



