<template>
    <div class="home">
        <div class="content">
            <ul class="chat-list" v-if="friendsList.length !=0">
                <li class="chat-list-item" v-for="(item, index) in friendsList" :key="index">
                    <router-link :to="{path:'/chatRoom', query: {peerID: item.userID, nickname: item.nickname}}">
                        <div class="avatar">
                            <img :src="'/cryptolalia/img/'+item.headImg" width="45" height="45" alt="">
                        </div>
                        <div class="info">
                            <p class="nickname">{{item.nickname}}</p>
                            <p class="chats">HelloWord</p>
                        </div>
                    </router-link>
                </li>
            </ul>
            <div class="noFriends" v-else>
                <p>暂无好友</p>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { Toast } from 'vant'
export default {
    name: 'home',
    data () {
        return {
            userID: JSON.parse(sessionStorage.getItem('user')).userID,
            friendsList: []
        }
    },
    methods: {
        
    },
    created () {
        axios.get('/cryptolalia/user/list/'+this.userID+'/'+1).then((res) => {
            console.log(res.data)
            if (res.data.length !=0) {
                this.friendsList = res.data
            }
        })
    },
    destroyed () {
        
    }
}
</script>

<style scoped>
.home {
    padding: 50px 0px;
}
li.chat-list-item a{
    display: flex;
    background: #fff;
    overflow: hidden;
}
.chat-list-item .avatar {
    width: 15%;
    display: flex;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 5px;
}
.chat-list-item .info {
    width: 80%;
    height: 100%;
    padding: 5px;
    box-sizing: border-box;
    border-bottom: 1px solid #ddd;
}
p.nickname {
    font-size: 15px;
    margin-bottom: 2px;
    color: #000;
}
p.chats {
    font-size: 12px;
    color: #999;
    margin-bottom: 5px;
}
.noFriends {
    padding: 10px;
}
</style>



