import Vue from 'vue'
import Router from 'vue-router'
import home from 'components/home/home'
import chatRoom from 'components/home/page/chatRoom'
import friends from 'components/friends/friends'
import user from 'components/user/user'
import login from 'components/login/login'
import register from 'components/register/register'
import add from 'components/add/add'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        title: '聊天室',
        isCommonShow: true,
      }
    },
    {
      path: '/friends',
      name: 'friends',
      component: friends,
      meta: {
        title: '通讯录',
        isCommonShow: true
      }
    },
    {
      path: '/user',
      name: 'user',
      component: user,
      meta: {
        title: '我的',
        isCommonShow: true
      }
    },
    {
      path: '/add',
      name: 'add',
      component: add,
      meta: {
        title: '好友添加',
        isCommonShow: true
      }
    },
    {
      path: '/chatRoom',
      name: 'chatRoom',
      component: chatRoom,
      meta: {
        isCommonShow: false
      }
    },
    {
      path: '/register',
      name: 'register',
      component: register,
      meta: {
        isCommonShow: false
      }
    },
    {
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        isCommonShow: false
      }
    }
  ]
})



