<template>
  <div id="app">
    <m-header v-show="$route.meta.isCommonShow" :title="$route.meta.title"></m-header>
    <router-view/>
    <m-footer v-show="$route.meta.isCommonShow"></m-footer>
  </div>
</template>

<script>
import mHeader from 'common/mHeader'
import mFooter from 'common/mFooter'
export default {
  name: 'App',
  components: {
    mHeader,
    mFooter
  }
}
</script>

<style>
html, body {
  width: 100%;
  min-height: 100%;
  font-family: PingFangSC-Regular, sans-serif;
  background: #f9f9f9;
}
  /*解决移动端滑动不顺畅问题*/
body{
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  overflow-y: scroll;
}
  #app {
    width: 100%;
    min-height: 100%;
  }
</style>



