import store from '../store/index'
var websock = null

export default {
    initWebSocket (userID) {
        const url = "ws://192.168.22.71/cryptolalia/websocket/"
        websock = new WebSocket(url + userID)
        websock.onopen = this.websocketonopen
        // 报错
        websock.onerror = this.websocketonerror
        // 接收数据
        websock.onmessage = this.websocketonmessage
        // 关闭
　　　　 websock.close = this.websocketclose
    },
    websocketonopen () {
        console.log('连接成功')
    },
    websocketonerror (e) {  // 错误
        Toast.fail('WebSocket连接发生错误')
    },
    websocketonmessage (e) {  // 接收数据
        const reData = e.data
        const oMessage = reData.split('?') 
        const obj = {
            userID: oMessage[1],
            message: oMessage[0]
        }
        if (oMessage[0] != '') {
            store.dispatch('getMessages', obj)
        }
    },
    websocketsend (sendDate) {  // 发送数据
        websock.send(sendDate)
        console.log(sendDate)
    },
    websocketclose (e) {  // 关闭
        console.log('关闭')
    }
}


