import axios from 'axios'
import { Toast } from 'vant'

// http request 拦截器
axios.interceptors.request.use(config=> {
  return config;
}, err=> {
  Toast.fail({message: '请求超时!'});
  return Promise.resolve(err);
})

// 响应拦截，异常处理
axios.interceptors.response.use(data=> {
  return data;
}, err=> {  // 异常处理
  if (err && err.response) {
    switch (err.response.status) {
      case 400:
          Toast.fail('错误请求')
        break;
      case 401:
          Toast.fail('未授权，请重新登录')
        break;
      case 403:
          Toast.fail('拒绝访问')
        break;
      case 404:
          Toast.fail('请求错误,未找到该资源')
        break;
      case 405:
          Toast.fail('请求方法未允许')
        break;
      case 408:
          Toast.fail('请求超时')
        break;
      case 500:
          Toast.fail('服务器端出错')
        break;
      case 501:
          Toast.fail('网络未实现')
        break;
      case 502:
          Toast.fail('网络错误')
        break;
      case 503:
          Toast.fail('服务不可用')
        break;
      case 504:
          Toast.fail('网络超时')
        break;
      case 505:
          Toast.fail('http版本不支持该请求')
        break;
      default:
          Toast.fail(`连接错误${err.response.status}`)
    }
  } else {
    Toast.fail('连接到服务器失败')
  }
  return Promise.resolve(err);
})

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function fetch(url,params={}){
  return new Promise((resolve,reject) => {
    axios.get(url,{
      params:params
    })
    .then(response => {
      resolve(response.data);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */
// 请求头
let headers = {headers: {'Content-Type':'multipart/form-data'}}

 export function upFile(url,data = {}){
   return new Promise((resolve,reject) => {
     axios.post(url,data, headers)
          .then(response => {
            resolve(response.data);
          },err => {
            reject(err)
          })
   })
 }

 export function post(url,data = {}){
  return new Promise((resolve,reject) => {
    axios.post(url,data)
         .then(response => {
           resolve(response.data);
         },err => {
           reject(err)
         })
  })
}

export const server = {
    login (paramObj) {  // 登陆
        return post('/cryptolalia/user/login', paramObj)
    },
    register (paramObj) {  // 注册
        return post('/cryptolalia/user/register', paramObj)
    }
}


